const map = {
  state: {
    map: '',
    position: ''
  },
  mutations: {
    CREATE_MAP: (state, map) => {
      state.map = map
    },
    SET_CENTER: (state, pos) => {
      state.position = pos
    },
    SET_MAP_LANGUAGE: (state, lang) => {
      void (lang === 'en' ? 'en' : lang = 'zh_cn')
      state.map.setLang(lang)
    }
  },
  actions: {
    createMap ({ commit }, map) {
      commit('CREATE_MAP', map)
    },
    setPosition ({ commit }, pos) {
      commit('SET_CENTER', pos)
    },
    setMapLanguage ({ commit }, lang) {
      commit('SET_MAP_LANGUAGE', lang)
    }
  }
}

export default map



