const getters = {
  errorLog: state => state.errorLog.logs,
  errorMsg: state => state.errorLog.errorMsg,
  map: state => state.amap.map,
  position: state => state.amap.position,
  sidebarShow: state => state.app.sidebarShow,
  loading: state => state.app.loading,
  loadingToast: state => state.app.loadingToast,
  globalToast: state => state.app.globalToast,
  toastText: state => state.app.toastText,
  globalMessageBox: state => state.app.globalMessageBox,
  messageBoxID: state => state.app.messageBoxID,
  globalActionSheet: state => state.app.globalActionSheet,
  actionSheetID: state => state.app.actionSheetID,
  isLogin: state => state.user.isLogin,
  userInfo: state => state.user.userInfo,
  chargerId: state => state.user.chargerId,
  bindPhone: state => state.user.bindPhone,
  bindWechat: state => state.user.bindWechat,
  bindAlipay: state => state.user.bindAlipay,
  mqttState: state => state.user.mqttState,
  unReturndevice: state => state.user.unReturndevice,
  scanFlag: state => state.user.scanFlag,
  msgSeconds: state => state.errorLog.msgSeconds,
  unpayed: state => state.user.unpayed,
  networkStatus: state => state.user.networkStatus

}

export default getters



