const user = {
  state: {
    isLogin: false,
    userInfo: {
      headPic: require('../../assets/personal_images/avatar.png')
    }, // 用户信息
    chargerId: '', // 网点id、机柜id
    bindPhone: false,
    bindWechat: false,
    bindAlipay: false, // 支付宝绑定
    mqttState: false, // mqtt状态
    unReturndevice: false, // 无未归还充电宝
    networkStatus: true, // 手机网络状态
    scanFlag: false, // 扫码时设为true， 执行完借还后为false // 在控制返回页面跳转
    unpayed: false // true 有未支付订单
  },
  mutations: {
    SET_LOGIN: (state, bol) => {
      state.isLogin = bol
    },
    USERINFO: (state, msg) => {
      // 合并相同属性加{}
      state.userInfo = Object.assign({}, state.userInfo, msg)
      console.log(state.userInfo)
    },
    SET_CHAEGERID: (state, obj) => {
      state.chargerId = obj
    }
  },
  actions: {
    setLogin ({commit}, bol) {
      commit('SET_LOGIN', bol)
    },

    setUserInfo ({commit}, msg) {
      commit('USERINFO', msg)
    },

    setChargerId ({commit}, obj) {
      commit('SET_CHAEGERID', obj)
    }

    // // 登录
    // LoginByUser ({commit}, userInfo) {
    //   return new Promise((resolve, reject) => {})
    // }
  }
}

export default user



