import axios from 'axios'
import store from './store/index'
import {
  NAVIGATOR
} from './utils/index'
import {
  setStorage
} from './utils/local'
import router from './router'
import { errorHandle } from './components/ErrorCode/errorCode'

let base = {}
// let timer
base.install = function (Vue) {
  //  HTTP请求
  Vue.prototype.$http = axios
  Vue.prototype.Version = 3 // 1 Ruioo 2 synerMax skin 3 sweden(引用synermax.less文件-->utils.js 配置文件
  // 调试模式
  Vue.prototype.ISDEBUG = (process.env.NODE_ENV === 'development')
  Vue.directive('focus', {
    inserted: function (el) {
      el.focus()
    }
  })
  /**
   * 数据交互请求接口
   * @method dataInteractionRequest
   * @param  {string}   data        需要发送的数据，必选
   */
  Vue.prototype.dataInteractionRequest = function (data) {
    console.log('Request:', data)
    if (Vue.prototype.ISDEBUG) return false
    if (!store.state.user.networkStatus) {
      errorHandle('1')
      return false
    }
    // clearTimeout()
    // if (data.msgType !== '0x5205' || data.msgType !== '0x5213' || data.msgType !== '0x5211' || data.msgType !== '0x5201') {
    //   store.state.errorLog.msgSeconds = false
    //   timer = setTimeout(() => {
    //     console.log(store.state.errorLog.msgSeconds)
    //     if (!store.state.errorLog.msgSeconds) {
    //       // alert(1)
    //       console.log('响应超时')
    //       errorHandle('500500')
    //       clearTimeout(timer)
    //     }
    //   }, 6000)
    // }
    switch (NAVIGATOR()) {
      case 'IOS':
        let obj = {
          'data': JSON.stringify(data)
        }
        window.webkit.messageHandlers.dataInteractionRequest.postMessage(obj)
        break
      case 'Android':
        window.Data.dataInteractionRequest(JSON.stringify(data))
        break
    }
  }

  /**
   * 数据交互响应接口
   * @method dataInteractionResponse
   */
  Vue.prototype.dataInteractionResponse = function (fn) {
    switch (NAVIGATOR()) {
      case 'IOS':
      case 'Android':
        window.dataInteractionResponse = function (data) {
          // clearTimeout(timer)
          store.state.errorLog.msgSeconds = true
          data = data.replace(/[\r\n]/g, ' ')
          let obj = JSON.parse(data)
          console.log('Response:', obj)
          // 错误码
          if (obj.data) {
            if (obj.data.errorCode) {
              errorHandle(obj.data.errorCode)
            }
          }
          // 隐藏加载框
          store.dispatch('loadingAlert', false)
          fn(obj)
          // console.log(router.currentRoute.fullPath)
          switch (obj.msgType) {
            case '0x5001':
              if (store.state.app.globalMessageBox) {
                store.dispatch('setGlobalBox', {bol: false, id: 0})
              } else if (store.state.app.globalActionSheet) {
                store.dispatch('setGlobalSheet', {bol: false, id: 0})
              } else {
                if (router.currentRoute.fullPath !== '/home') {
                  router.back()
                } else {
                  if (store.state.app.sidebarShow) {
                    store.dispatch('sidebarSwitch', false)
                  } else {
                    // 按返回物理按键退出请求
                    Vue.prototype.dataInteractionRequest({
                      msgType: '0x5002'
                    })
                  }
                }
              }
              break
            // 网络状态返回
            case '0x5032':
              if (obj.result && obj.data) {
                // store.dispatch('setLanguage', obj.data.language)
                if (obj.data.status) {
                  store.state.user.mqttState = true
                  // Vue.prototype.dataInteractionRequest({
                  //   msgType: '0x5151'
                  // })
                  // Vue.prototype.dataInteractionRequest({
                  //   msgType: '0x5221',
                  //   type: 3,
                  //   page: 1,
                  //   count: 10
                  // })
                } else {
                  store.state.user.mqttState = false
                }
              }
              break
            case '0x5033':
              if (obj.result && obj.data) {
                if (obj.data.state !== 1) {
                  store.state.user.networkStatus = false
                } else {
                  store.state.user.networkStatus = true
                }
              }
              break
            // 系统语言变更返回
            case '0x5043':
              if (obj.result && obj.data) {
                store.dispatch('setLanguage', obj.data.language)
              }
              break
            // 版本更新返回
            case '0x5025':
              if (obj.result) {
                // 全局吐司
                store.dispatch('setGlobalToast', {
                  bol: true,
                  zh: `${obj.data.progress}%`,
                  en: `${obj.data.progress}%`
                })
                if (obj.data.status === 2) {
                  store.dispatch('setGlobalToast', {
                    bol: true,
                    zh: '更新完成！',
                    en: 'Update complete!'
                  })
                } else if (obj.data.status === 0) {
                  store.dispatch('setGlobalToast', {
                    bol: true,
                    zh: '暂停更新',
                    en: 'Suspend update'
                  })
                }
              }
              break
            // 归还充电宝
            case '0x5212':
              if (obj.result) {
                router.push('/restituteSuccessfully')
              }
              break
            // 借出归还充电宝统一返回
            case '0x5214':
              if (obj.result) {
                store.state.user.scanFlag = false
                if (obj.data.type === 'RETURN') {
                  Vue.prototype.dataInteractionRequest({
                    msgType: '0x5223',
                    no: obj.data.no
                  })
                  router.push({name: 'restituteSuccessfully', params: {data: obj.data}})
                }
              }
              break
            // 修改用户信息返回
            case '0x515F':
              if (obj.result && obj.data) {
                store.dispatch('setUserInfo', obj.data)
              }
              break
            // 微信支付宝绑定返回
            case '0x5152':
              if (obj.result) {
                if (obj.data.mobile !== '' && obj.data.mobile !== undefined) {
                  store.state.user.bindPhone = true
                }
                if (!obj.data.thirdInfos) return false
                obj.data.thirdInfos.forEach(element => {
                  if (element.type === 10 || element.type === 18) {
                    store.state.user.bindWechat = true
                  } else if (element.type === 11) {
                    store.state.user.bindAlipay = true
                  }
                })
              }
              break
            case '0x5226':
              if (data.result) {
                if (data.data.type === 'LENT') {
                  this.$store.state.user.unReturndevice = false
                } else if (data.data.type === 'RETURN') {
                  this.$store.state.user.unReturndevice = true
                }
              }
              break
            case '0x5224':
              if (data.result) {
                let obj = data.data
                setStorage('biiinfo', obj)
              }
              break
          }
          console.log('base End')
        }
        break
    }
  }
}
export default base



