import i18n from '../../lang'
let timer
const app = {
  state: {
    sidebarShow: false, // 侧边栏
    loading: false, // 加载框
    loadingToast: false,
    globalToast: false, // 全局吐司
    toastText: '',
    globalMessageBox: false, // 弹框
    messageBoxID: 0,
    globalActionSheet: false, // 操作表
    actionSheetID: 0
  },
  mutations: {
    OPEN_SIDEBAR: (state, value) => {
      state.sidebarShow = value
    },
    LOADING_ALERT: (state, bol) => {
      state.loading = bol
    },
    SET_LANGUAGE: (state, lang) => {
      i18n.locale = lang
    },
    SET_GLOBAL_TOAST: (state, obj) => {
      state.globalToast = obj.bol
      state.toastText = obj[i18n.locale]
    },
    SET_GLOBAL_BOX: (state, obj) => {
      state.globalMessageBox = obj.bol
      state.messageBoxID = obj.id
    },
    SET_GLOBAL_SHEET: (state, obj) => {
      state.globalActionSheet = obj.bol
      state.actionSheetID = obj.id
    }
  },
  actions: {
    sidebarSwitch ({ commit }, value) {
      commit('OPEN_SIDEBAR', value)
    },
    loadingAlert ({commit, state}, bol) {
      commit('LOADING_ALERT', bol)
      if (bol) {
        timer = setTimeout(() => {
          state.loading = false
          state.loadingToast = true
          clearTimeout(timer)
        }, 20000)
      } else {
        clearTimeout(timer)
        state.loading = false
      }
    },
    setLanguage ({commit}, lang) {
      commit('SET_LANGUAGE', lang)
      commit('SET_MAP_LANGUAGE', lang)
    },
    setGlobalToast ({commit}, obj) {
      commit('SET_GLOBAL_TOAST', obj)
    },
    setGlobalBox ({commit}, obj) {
      commit('SET_GLOBAL_BOX', obj)
    },
    setGlobalSheet ({commit}, obj) {
      commit('SET_GLOBAL_SHEET', obj)
    }
  }
}

export default app



